@font-face {
  font-family: AvenirBook;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 400;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 300;
}

* {
  box-sizing: border-box;
}

html {
  --border-color: #e1e7ed;
  --text-color: #3d4e61;
  --white-color: #fff;
  font-family: Avenir, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.sch-bar {
  margin-top: 20px;
  position: relative;
}

.sch-bar:hover {
  background: var(--white-color);
  border-radius: 4px;
  box-shadow: 0 3px 8px #3d4e6133;
}

.sch-bar__input {
  border: 1px solid var(--border-color);
  color: var(--text-color);
  border-radius: 4px;
  width: 100%;
  font-size: 14px;
  position: relative;
  box-shadow: 0 1px 8px #3d4e611a;
}

.sch-bar__input:focus {
  background: linear-gradient(180deg, var(--white-color) 0%, #f6f6f7 100%);
  border-radius: 3px;
  outline: none;
}

.sch-bar__input::placeholder {
  color: var(--text-color);
  font-family: AvenirBook, Helvetica, sans-serif;
  font-weight: 300;
}

.sch-bar--big input {
  color: var(--text-color);
  height: 70px;
  padding-left: 62px;
  font-family: inherit;
  font-size: 16px;
}

.sch-bar--small input {
  color: var(--text-color);
  height: 42px;
  padding-left: 33px;
  font-family: inherit;
  font-size: 14px;
}

.sch-bar--small:before {
  z-index: 1;
  content: "";
  background: url("Search.ccd3c07b.svg") 0 0 / contain no-repeat;
  width: 11px;
  height: 11px;
  display: inline-block;
  position: absolute;
  top: 15px;
  left: 13px;
}

.sch-bar--big:before {
  z-index: 1;
  content: "";
  background: url("Search.ccd3c07b.svg") 0 0 / contain no-repeat;
  width: 19px;
  height: 19px;
  display: inline-block;
  position: absolute;
  top: 25px;
  left: 26px;
}

